



































































@import "../styles/variables";

.notification-container{
  position: sticky;
  top: $header-height;
  z-index: 10;
  max-width: 450px;
  margin-left: auto;
  margin-top: $header-height;

  &.fixed {
    position: fixed;
    top: $header-height;
  }

  .notification-box {
    position: relative;
  }

  .notifications {
    position: absolute;
    background-color: white;
  }
}

.notification {
  position: relative;
  padding: $spacing;

  font-size: $font-size-small;

  border-left: 5px solid $app-color;
  display: flex;
  align-items: center;

  .notification-text {
    flex-grow: 1;

    .notification-title {
      font-weight: 600;
    }

    .notification-message {
      white-space: pre-line;
    }
  }

  .notification-close {
    margin: auto;
    cursor: pointer;

    .notification-icon {
      width: $icon-medium;
      max-height: $icon-medium;
      vertical-align: middle;
      margin-left: $spacing;
    }
  }

  .notification-status-icon {
    width: $icon-large;
    max-height: $icon-large;
    margin-right: $spacing;
    fill: $font-color-second;
    stroke: $font-color-second;
    flex-shrink: 0;
  }
}

.notification-wrapper {
  box-shadow: 0 3px 3px rgba(0, 0, 0, .05);

  &:not(:first-child) {
    border-top: $separation-line;
  }
}

@media screen and (max-width: $mobile) {
  .notification-container {
    max-width: 100%;
  }
}
